.App {
  text-align: center;
}

.App-logo {
  width: calc(165px + 2vmin);
  height: 63.99px;
  pointer-events: none;
}
.App-name {
  font-family: 'Manrope-bold'!important;

  font-size: calc(12px + 1vmin)!important;
}
.App-logo-square {
  width: calc(30px + 2vmin);
  height: 63.99px;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #FFFFFF;
  min-height: 15vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  /* padding: 0px 40px; */
  justify-content: flex-start;
  font-size: calc(10px + 2vmin);
  color: #000;
}

.App-footer {
  margin: 0 auto;
}

.App-link {
  color: #61dafb;
}

.App-content {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  /* align-items: center; */
  justify-content: flex-start;
  font-size: calc(10px + 2vmin);
  padding: 8px 20px;
  word-break: normal;
}

.App-content-home {
  padding-top: calc(100px - 1.5vmin);
}

.App-content-papers {
  font-size: 16px;
  text-align: left;
}

.Main-title {
  font-size: calc(40px + 2vmin);
  /* color: #FFFFFF; */
  margin-bottom: 20px;
}

@media screen and (max-width: 980px) {
  .App-content {
    /* word-break: break-all; */
    word-break: normal;
  }
}

@media screen and (max-width: 600px) {
  .Main-title {
    font-size: calc(20px + 2vmin);
  }
}
/** problem with breaking rendering */
@media screen and (min-width: 600px) {
  .App-header-papers {
    margin-left: 240px;
  }
  .App-content-papers {
    margin-left: 240px;
  }
}

.Main-subtitle {
  color: #767682;
  font-size: calc(8px + 2vmin);
  margin-top: 0px;
}

