/* Override Mui */
.Mui-expanded {
  color: #317AFF;
  margin: 0 5px;
}
.Mui-expanded #expandIcon {
  color: #FFFFFF;
}
.MuiDrawer-root {
  border-right: 1px solid #fff;
}
.MuiDrawer-paper {
  border-right: 1px solid #606770!important;
}
.MuiToolbar-root {
  padding: 0px 20px!important;
}
.MuiList-root {
  padding: 8px 5px!important;
}
.MuiListItemButton-root {
  border-radius: 8px!important;
}
.MuiListItemText-root {
  /* word-break: break-word; */
  word-break: normal;
  color: #FFFFFF;
  font-family: "Manrope-bold"
}
.Mui-selected {
  background-color: #25272b;
}
.Mui-selected .MuiTypography-root {
  color: #317AFF!important;
}
h1 {
  margin-block-start: 0px!important;
  margin-block-end: 8px!important;
}
p {
  line-height: 1.5;
  word-break: keep-all;
}
li {
  line-height: 1.5;
}
a {
  color: #5a94ff;
  padding: 0px 2px;
}
code {
  background-color: #F6F8FA;
  padding: 8px 8px;
  line-height: 1.5;
  border-radius: 8px;
  color: #000;
  display: block;
  white-space: pre-wrap;
  overflow-x: scroll;
}
table, th, td {
  border: 1px solid;
  padding: 0.75rem;
}
table {
  scroll-behavior: smooth;
  table-layout: fixed;
  border-collapse: collapse;
  border: 0.5px solid #DADDE1;
  padding: 0.75rem;
  line-height: 1.5;
  width: 100%; 
  /* word-break: break-word; */
  word-break: normal;
}
table thead tr {
  border: 2px solid #DADDE1!important;
  border-bottom: 2px!important;
  border-bottom-color: #DADDE1!important;
  border-bottom-style: solid!important;
}

em, a {
  /* word-break: break-all;*/
  word-break: normal;
}
h1 {
  /* word-break: break-word; */
  word-break: normal;
}
ol {
  padding-left: 16px;
}


.CustomComp {
  margin: 0 auto;
}

.App-footer {
  background-color: #FFFFFF;
  padding: 10px 20px;
  text-align: left;
  color: #000000;
  border-top: 0.5px solid #EDEDED;
}

.App-footer-light {
  background-color: #f7f7ff!important;
}
